.time-clock{
  position: absolute;
  top: 10px;
  left: 0;
  display: flex;
  align-items: center;
}
.current-time{
	font-size: 14px;
  font-weight: 600;
  color: #e5e5e5;
  margin: 0 5px;
}
.fa-clock{
	color: #e5e5e5;
	margin-left: 20px;
	font-size: 18px;
}
.time-clock span{
	color: #e5e5e5;
	font-size: 14px;
	font-weight: 600;
}
.lang-bar{
  position: absolute;
  right: -3px;
  top: 8px;
  z-index: 99;
}
.lang-bar span{
	background-color: #7b0005;
  padding: 10px;
  color: #fff;
  margin-right: 3px;
  cursor: pointer;
  border-radius: 0px;
  -webkit-transition: ease .2s;
  transition: ease .2s;
}
.lang-bar span:hover{
	background-color: #cc2027;
}
/*---------------------------Upper Header-------------*/
.nav-link{
	font-family: OpenSans;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #e5e5e5;
  text-transform: uppercase;
  margin-right: 5px;
  padding: 10px 15px!important;
  -webkit-transition: ease .2s;
  transition: ease .2s;
  cursor: pointer;
}
.nav-link:hover, .nav-link:focus{
	background-color: #cc2027;
	color: #fff;
}
.navbar{
	padding: 0;
	background-color: #222222;
	min-height: 41px;
}
.navbar-brand img{
	width: 200px;
}
.active{
	background-color: #cc2027!important;
}
.navbar-collapse{
	justify-content: center;
}
/*---------------------------Down Header-------------*/
.logo-box{
	background-image: -webkit-linear-gradient(bottom, #840000, #cc2027);
	background-image: linear-gradient(to top, #840000, #cc2027);
	padding: 10px 0;
}
.register{
	font-size: 18px;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
  box-shadow: 2.5px 1.7px 0 0 rgba(0, 0, 0, 0.5);
  background-color: #313131;
  padding: 5px 20px;
  -webkit-transition: ease .2s;
  transition: ease .2s;
}
.register:hover, .register:focus{
	color: #fff;
	background-color: #222;
}
.logo-box-content{
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 90px;
}
/*-------------------------Slider--------------------*/
.slide img{
	width: 100%;
}
.slider-overlay img{
	width: 280px;
	margin: 120px 0 40px;
}
.slider-overlay h2{
	font-size: 42px;
  font-weight: 100;
	color: rgba(255, 255, 255, 0.8);
  text-transform: uppercase;
  max-width: 500px;
  font-weight: 300;
  margin: 0 0 70px;
}
.slider-overlay p{
	max-width: 600px;
	font-size: 17px;
  font-weight: 300;
  line-height: 1.43;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 45px;
}
.main-slider{
	position: relative;
	overflow-x: hidden;
}
.slider-overlay{
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 9%;
	color: #fff;
}
.slider-register{
  display: inline-block;
  padding: 14px 30px;
  background-color: #c91f25;
  border-radius: 4px;
  font-size: 17px;
  font-weight: 600;
  color: #ffffff;
  margin-right: 8px;
  cursor: pointer;
  min-width: 174px;
  text-align: center;
  -webkit-transition: ease .2s;
  transition: ease .2s
}
.slider-log-in{
	display: inline-block;
	padding: 12px 28px;
	border-radius: 4px;
  border: solid 2px #ffffff;
  background-color: rgba(49, 49, 49, 0.5);
  font-size: 17px;
  min-width: 174px;
  text-align: center;
  cursor: pointer;
  -webkit-transition: ease .2s;
  transition: ease .2s
}
.slider-log-in:hover{
	background-color: #fff;
	color: rgba(49, 49, 49, 1);
}
.slider-register:hover{
	background-color: #940f14;
}







body{
	font-family: 'Open Sans', sans-serif;
}
ul,li,a,h1,h2,h3,h4,h5,p,span{
	font-family: 'Open Sans', sans-serif;
}
ul{
	list-style: none;
	padding: 0;
	margin: 0;
}
a:hover{
	text-decoration: none;
}
footer{
	text-align: center;
	padding: 25px 0 20px;
	background-color: #222222;
}
.list-title{
  font-size: 13px;
  color: #76767b;
}
.payment-list{
	margin-bottom: 40px;
}
.payment-list img{
	max-height: 35px;
}
.payment-list li{
	margin: 0 2px;
	display: inline-block;
}
.partner-list{
	max-width: 900px;
	margin-left: auto;
	margin-right: auto;
}
.partner-list li{
	display: inline-block;
	margin: 20px 8px;
}
.partner-list img{
	max-height: 80px;
}
.copy-right p{
	color: #6d6d6d;
	font-size: 12px;
	margin-bottom: 15px;
	line-height: 1.3;
}
